"use client";

import { useEffect, useState } from "react";

// TypeScript type for a transaction
type Transaction = {
  id: number;
  merchant_name: string;
  merchant_address: string;
  txn_date: string;
  txn_time: string;
  mid: number;
  tid: number;
  batch_no: number;
  stan: number;
  txn_type: string;
  pan_number: string;
  exp_date: string;
  invoice_number: number;
  card_type: string;
  entry_mode: string;
  approval_code: number;
  rrn: string;
  aid: string;
  application_name: string;
  tvr: string;
  tsi: string;
  amount: number;
  email: string;
  mobile: number;
  created_at: string;
};

const TransactionsPage = () => {
  const [transactions, setTransactions] = useState<Transaction[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string>("");

  // Fetch data from API
  useEffect(() => {
    const fetchTransactions = async () => {
      try {
        const response = await fetch("http://127.0.0.1:8000/transactions"); // replace with your FastAPI endpoint if different
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        const data = await response.json();
        setTransactions(data.data); // match the "data" key in your JSON
        setLoading(false);
      } catch (err: any) {
        setError(err.message);
        setLoading(false);
      }
    };

    fetchTransactions();
  }, []);

  if (loading) return <p>Loading transactions...</p>;
  if (error) return <p>Error: {error}</p>;

  return (
    <div className="">
      <h1 className="text-2xl font-bold mb-4">Transactions</h1>
      <div className="overflow-x-auto">
        <table className="min-w-full border border-gray-300">
          <thead className="bg-gray-200">
            <tr>
              <th className="px-4 py-2 border">ID</th>
              <th className="px-4 py-2 border">Merchant</th>
              <th className="px-4 py-2 border">Address</th>
              <th className="px-4 py-2 border">Date</th>
              <th className="px-4 py-2 border">Time</th>
              <th className="px-4 py-2 border">Card Number</th>
              <th className="px-4 py-2 border">Amount</th>
              <th className="px-4 py-2 border">Email</th>
              <th className="px-4 py-2 border">Mobile</th>
            </tr>
          </thead>
          <tbody>
            {transactions.map((txn) => (
              <tr key={txn.id} className="text-center">
                <td className="px-4 py-2 border">{txn.id}</td>
                <td className="px-4 py-2 border">{txn.merchant_name}</td>
                <td className="px-4 py-2 border">{txn.merchant_address}</td>
                <td className="px-4 py-2 border">{txn.txn_date}</td>
                <td className="px-4 py-2 border">{txn.txn_time}</td>
                <td className="px-4 py-2 border">{txn.pan_number}</td>
                <td className="px-4 py-2 border">{txn.amount.toFixed(2)}</td>
                <td className="px-4 py-2 border">{txn.email}</td>
                <td className="px-4 py-2 border">{txn.mobile}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default TransactionsPage;
